import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage, StaticImage } from "gatsby-plugin-image"
import styled from '@emotion/styled'

import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from 'gatsby-background-image'

import Box from '../../../components/Box'
import { Media } from '../../../components/ThemeProvider/theme'

const GatsbyBgImage = styled(BackgroundImage)`
height: 100%;
width: 100%;
`

const Banner = () => {
  const { mobile, desktop } = useStaticQuery(
    graphql`
      query {
        mobile: file(relativePath: { eq: "containers/HomePage/Banner/mobile.jpg" }) {
          childImageSharp {
            gatsbyImageData
          }
        }
        desktop: file(relativePath: { eq: "containers/HomePage/Banner/desk.jpg" }) {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    `
  )
  const imageMobile = getImage(mobile)
  const bgImageMobile = convertToBgImage(imageMobile)
  const imageDesk = getImage(desktop)
  const bgImageDesk = convertToBgImage(imageDesk)

  return (
    <Box.Relative height="100%" width="auto">
      <Box as={Media} lessThan="tablet" height="100%">
        <GatsbyBgImage {...bgImageMobile} />
        <Box.FullAbs>
          <StaticImage src="mobile_text.png" alt="越永續，越得意" />
        </Box.FullAbs>
      </Box>
      <Box as={Media} greaterThanOrEqual="tablet" height="100%">
        <GatsbyBgImage {...bgImageDesk} />
      </Box>
    </Box.Relative>
  )
}

export default Banner
